import axios from '@/utils/uniAxios.js'
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		// uniCard
	},
	data() {
		return {
			shoucang:0,
			zuji:0,
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			abImg:'../../static/banner_01.png',
			goodsList:[
				{
					img:'../../static/egg_01.png',
					title:'正宗农家散养谷饲土鸡蛋20枚',
					subTitle:'宇宙无敌巨好吃的鸡蛋',
					jifen:'200',
					price:999.90
				},
				{
					img:'../../static/egg_01.png',
					title:'正宗农家散养谷饲土鸡蛋20枚',
					subTitle:'宇宙无敌巨好吃的鸡蛋',
					jifen:'200',
					price:999.90
				},
				{
					img:'../../static/egg_01.png',
					title:'正宗农家散养谷饲土鸡蛋20枚',
					subTitle:'宇宙无敌巨好吃的鸡蛋',
					jifen:'200',
					price:999.90
				}
			],
			authShow:true,
		}
	},
	onLoad(option) {
		this.loadData();
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		// 个人资料
		accountManage(){
			if(uni.getStorageSync('setPhone')||uni.getStorageSync('gt')){
				uni.navigateTo({
					url:'../set/set?id='+this.userInfo.id 
				})
			}else{
				uni.navigateTo({
					url:'../public/login'
				})
			}
		},
		// 从登录页跳转过来，刷新页面,获取用户信息
		loadData(){
			// 如果使用手机号登录
			let userInfos = uni.getStorageSync('userInfo')
			if(uni.getStorageSync('gt')&&uni.getStorageSync('setPhone')){
				axios.post('/sso/user/userInfo').then(res=>{
					if(res.data.code='200'){
						this.authShow = false;
						this.userInfo.mobile = res.data.data.phone;
						this.userInfo.id = res.data.data.id;
						
						this.$store.commit('login',this.userInfo)
					}
				})
			}else if(userInfos.nickName){
				console.log(userInfos.nickName)
				this.authShow = false;
				this.userInfo.nickName = userInfos.nickName
				
			}
			// 如果使用微信登录方式登录
			
		},
			
		// 查看更多订单
		lookOrder() {
			uni.navigateTo({
				url: '../order/order?state=0'
			});
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			console.log(this.hasLogin)
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	}
}
